

.header_main {
  width: 1200px;
  margin-left :auto;
  margin-right: auto;
  display: block;
  border: solid black 1px;
}

.header_main_Logo>a>img{
  display: block;
  width: 20%;
  height: 20%;
}

.header_main_Logo{
  margin-left :auto;
  margin-right: auto;
  display: block;
}

.header_main_LogoAndInfo{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.header_main_LogoAndInfo>div{
  display: inline;
}